.modal-dialog {
    position: fixed;
    top: 50%;
    left: 50%;
    min-width: 300px;

    text-align: center;
    color: darkred;
    font-weight: 700;

    transform: translate(-50%);

    p {
        margin: 0;
    }
}

.modal-content {
    box-shadow: 0 0 5px 2px rgba(139, 0, 0, 0.5);
}
